html {
  font-size: 62.5%; /* 16px x 62.5 = 10px = 1rem */
  box-sizing: border-box;
}

body {
  margin: 0;
  height: 100vh;
  background-color: rgb(240, 238, 238);
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  height: 100%;
}

/* Layout */
.App {
  display: grid;
  grid-template-areas:
    "header"
    "main"
    "footer";
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%;
}
header {
  grid-area: header;
  background-color: #8967fc;
}
main {
  grid-area: main;
  padding: 1rem;
}
.App-Header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.App-Header.center {
  justify-content: center;
  font-size: 2.6rem;
}
/* Common */
h1 {
  font-size: 1.8rem;
  text-align: center;
}
h2 {
  font-size: 2rem;
  padding: 1rem 0;
  color: black;
}

a {
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
}
/* Header */
header a {
  color: #ffffff;
  padding: 1rem;
  font-size: 1.4rem;
  margin: 0.5rem 2rem;
}
header h2 {
  color: white;
  font-size: 2.5rem;
  margin: 0.5rem 2rem;
}
ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
li {
  margin-top: 0.5rem;
}
input,
select,
textarea,
button {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 0.1rem #c0c0c0 solid;
  font-size: 1.6rem;
  font-family: Bahnschrift Light, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
button {
  cursor: pointer;
  width: 40%;
  outline: none !important;
  color: white;
}
input:hover,
select:hover,
textarea:hover,
button:hover {
  border: 0.1rem #030202 solid;
}

button.primary {
  background-color: #8967fc;
  border-radius: 30rem;
  margin: auto;
}
button.block {
  width: 100%;
}

/* Form */
.form {
  max-width: 40rem;
  margin: 4.5rem auto;
  border: 0.2rem #d3d3d3 solid;
  border-radius: 0.5rem;
  background-color: white;
}
.form > div {
  display: flex;
  flex-direction: column;
  margin: 1rem;
}
.form label {
  margin: 1rem 0;
  display: block;
  font-size: 1.6rem;
}

.logo {
  width: 30%;
  display: block;
  text-align: center;
  margin: 20px auto;
}
/* Dropdown */
.dropdown {
  display: inline-block;
  position: relative;
}
.dropdown-content {
  position: absolute;
  display: none;
  right: 0;
  min-width: 16rem;
  padding: 1rem;
  z-index: 1;
  background-color: #8967fc;
  margin: 0;
  margin-top: 0.4rem;
  border-radius: 0.5rem;
}
.dropdown:hover .dropdown-content {
  display: block;
  color: white;
}
.loading {
  display: block !important;
}
.success {
  color: limegreen;
}
.danger {
  color: red;
}
.alert {
  padding: 1rem;
  border: 0.1rem solid transparent;
  border-radius: 0.5rem;
  font-size: 1.2rem;
}
.alert-info {
  color: #86de6d;
  background-color: #e0e0ff;
}
.alert-danger {
  color: red;
  background-color: #e0e0ff;
}

@media only screen and (max-width: 600px) {
  .App-Header h2 {
    font-size: 1.4rem;
    margin-left: 1rem;
  }
  .dropdown {
    margin-right: 0.1rem;
  }
}
